import tuiIcon from "@/components/icon/icon"
import {
	ajax,
	getSecret
} from '@/common/ajax/ajax.js'
export default {
	components: {
		tuiIcon
	},
	data() {
		return {
			openid: '',
			// 商品分类ID
			fid: 160,
			mid: '',
			small: '',
			pageid: 1,
			secret: '',
			timestamp: '',
			showCategoryIndex: 160,
			headerPosition: "fixed",
			tabbar: [],
			winHeight: "", //窗口高度
			currentTab: 0, //预设当前项的值
			scrollLeft: 0, //tab标题的滚动条位置
			showHeader: true,
			//分类列表
			categoryList: [],
			productList: [],
			pagecount: '',
			// 显示全部分类
			allShow: false,
			stateTab: true,
			pronum: 0,
			loadingText: '正在加载中',
			scrollTop: 0,
		}
	},

	onPageScroll(e) {
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},

	onLoad() {
		this.getList()
		var text = getSecret();
		this.secret = text.secret;
		this.timestamp = text.timestamp;
		uni.getStorage({
			key: 'user',
			success: (res) => {
				this.openid = res.data.openid;
			}
		});
		this.getbig()

	},
	onShow() {
		// this.productList = [];
		// this.getList();
		
		let index = uni.getStorageSync('cateSelect')
		if (index) {
			this.productList = []
			this.fid = index;
			this.showCategoryIndex = index;
			this.getList();
		}
	},
	onHide() {
		uni.removeStorageSync('cateSelect');
	},
	methods: {
		loadMore() {
			this.pageid++;
			if (this.pageid <= this.pagecount) {
				this.loadingText = '正在加载中...';
				setTimeout(() => {
					this.getgoodList();
					this.scrollTop = 0;
				}, 1000);
			} else {
				this.loadingText = '到底了';
			}
		},
		// 点击标题切换当前页时改变样式
		swichNav(e) {
			if (this.currentTab == e) {
				return false;
			} else {
				this.currentTab = e
			}
			this.mid = e;
			this.pageid = 1;
			this.productList = [];
			this.getgoodList();
		},
		isshow() {
			this.allShow = true
		},
		close() {
			this.allShow = false;
		},
		swichall() {				
			this.mid = '';
			this.currentTab = 0;
			this.pageid = 1;
			this.productList = [];
			this.getgoodList();
		},
		// 获取分类商品
		getList() {
			let data={
				fid: this.fid
			}
			this.$xm.post('/Procate/getMid',data,(res)=>{
				console.log(res);
				this.tabbar = res;
				this.getgoodList()
			})
			
		},
		// 获取商品总分类
		getbig() {
			this.$xm.post('/Procate/getBig','',(res)=>{
				console.log(res);
				this.categoryList = res;
			})
			
		},
		// 获取商品
		getgoodList() {				
			let data={
				big: this.fid,
				mid: this.mid,
				small: this.small,
				pageid: this.pageid
			}
			this.$xm.post('/Product/proList',data,(res)=>{
				let result = res.prolist;
				if(res.prolist.length==0){
					this.loadingText='没有更多商品！'
				}else{
					this.loadingText='正在加载中...'
				}
				result.map((ele) => {
					ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300'
					// if (ele.pro_name.length > 15) {
					// 	ele.pro_name = ele.pro_name.slice(0, 1) + '...'
					// }
					ele.itemcount = 0;
					
				})
				this.productList = this.productList.concat(result);
				this.pagecount = res.count;					
				console.log(this.productList)
			})
			
		},
		//分类切换显示
		showCategory(index) {
			this.loadingText = '正在加载中...';
			this.productList = [];
			this.showCategoryIndex = index;
			this.fid = index;
			this.currentTab = 0;
			this.mid = '';
			this.pageid = 1;
			this.getList()
		},
		//商品跳转
		toGoods(e) {
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.id
			});
		},
		// 加入购物车
		joinCart(e) {				
			e.itemcount++;
			let data = {
				proid: e.id,
				pronum: 1			
			};
			this.$xm.post('/Cart/add',data, (res) => {
				let result = res.msg;
				if (result == '加入购物车成功') {
					uni.showToast({
						title: "已加入购物车"
					});
				}
			})				
		},
		//搜索跳转
		toSearch() {
			uni.navigateTo({
				url: '../../search/search/search'
			})
		}
	}
}
